<template>
	
    <div class="login-wrap">
		<vue-particles
			color="#fff"
			:particleOpacity="0.7"
			:particlesNumber="60"
			shapeType="circle"
			:particleSize="4"
			linesColor="#fff"
			:linesWidth="1"
			:lineLinked="true"
			:lineOpacity="0.4"
			:linesDistance="150"
			:moveSpeed="2"
			:hoverEffect="true"
			hoverMode="grab"
			:clickEffect="true"
			clickMode="push"
			class="lizi"
		>
		</vue-particles>
        <div class="ms-login">
            <div class="ms-title">自动化测试平台</div>
            <el-form ref="loginForm" :model="loginForm" :rules="rules" label-width="0px" class="ms-content">
                <el-form-item prop="username">
                    <el-input v-model="loginForm.username" placeholder="username">
                        <el-button slot="prepend" icon="el-icon-user"></el-button>
                    </el-input>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input
                        type="password"
                        placeholder="password"
                        v-model="loginForm.password"
                        @keyup.enter.native="onSubmit"
                    >
                        <el-button slot="prepend" icon="el-icon-key"></el-button>
                    </el-input>
                </el-form-item>
                <div class="login-btn">
                    <el-button type="primary" @click="onSubmit">登录</el-button>
                </div>
                <p class="login-tips">Tips : 未注册用户请联系管理员添加</p>
            </el-form>
        </div>
		
    </div>
</template>

<script type="text/ecmascript-6">
export default {
    data() {
		//手机号码验证器
		let validateTel = (rule, value, callback) => {
			if(!value.match(/^1[0-9]{10}$/)){
				callback(new Error("请输入正确的手机号码！"))
			}else{
				callback()
			}
		}
        return {
			loginForm: {
				username:"",
				password:"",
			},
			rules: {
				username:[
					//为空时，提示信息，光标移出文本框后触发
					{required:true,message: "请输入手机号码！",trigger:"blur"},
					//调用验证器validateTel
					{validator:validateTel,trigger:"blur"}
				],
				password: [
					{required: true, message:"请输入密码！",trigger:"blur"},
					{min:6, max: 20, message:"密码长度应该在6～20之间！",trigger:"blur"}
				]
			}
		}
    },
    methods: {
        onSubmit(){
			this.$refs['loginForm'].validate(valid => {
				if(!valid){
					return;
				}
				this.$http.login(this.loginForm).then(res =>{
					if(res && res.data.token){
						//登录成功后获取用户信息
						const data = res.data
						const token = data.token
						const user = data.user
						this.$auth.setUserToken(user,token)
						this.$router.push('/')
						this.$message.success('恭喜！登录成功!')
					}else{
						this.$message.info('用户名或密码错误!')
					}
				})
			})
		}
	}
}
</script>

