<template>
<div class="">
    <div class="top-group">
        <el-button type="primary" icon="el-ico-plus" @click="onAddProjectButtonClick">新增host</el-button>
    </div>

    <el-table :data="project.host_list" style="width:100%">
        <el-table-column prop="name" label="名称" >
            <template slot-scope="scope">
                <!-- 新增鼠标移动到名称位置显示全部 -->
                <el-popover
                    placement="top-start"
                    width="200"
                    trigger="hover">
                    <slot>{{scope.row.name}}</slot>
                    <el-button slot="reference" type="text">{{scope.row.name|ellipsis}}</el-button>
                </el-popover>          
            </template>
        </el-table-column>
        <el-table-column prop="host" label="Host"></el-table-column>
        <el-table-column prop="description" label="描述" ></el-table-column>
        <el-table-column label="操作" width="300">
            <template slot-scope="scope">
                <el-button @click="onEditProject(scope.row)" size="mini">编辑</el-button>
                <el-button @click="deleteEditProject(scope.row.id,scope.$index)" type="danger" size="mini">删除
                </el-button>
            </template>
        </el-table-column>
    </el-table>  

    <el-dialog title="新增host" :visible.sync="hostDialogVisible">
        <el-form ref="hostForm" :model="hostForm" label-width="80px" :rules="hostRules">
            <el-form-item label="名称" prop="name">
                <el-input v-model="hostForm.name" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="host" prop="host">
                <el-input v-model="hostForm.host" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="描述" prop="description">
                <el-input type="textarea" v-model="hostForm.description" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="hostDialogVisible=false">取 消</el-button>
            <el-button type="primary" @click="onSubmitAddHost">确 定</el-button>
        </div>
    </el-dialog>
</div>
</template>

<script type="text/ecmascript-6">
import Vue from "vue";
export default {
    name: "HostList",
    props:['project'],
    filters: {
        ellipsis (value) {
        if (!value) return ''
        if (value.length > 8) {
            return value.slice(0,8) + '...'
        }
        return value
        }
    },
    data() {
        return {
            host_list:[],
            hostDialogVisible:false,
            hostForm:{
                id:"",
                name:"",
                host:"",
                description:""
            },
            hostRules:{
                name: [
                    {required:true, message:"请输入host名称！",trigger:"blur"}
                ],
                host: [
                    {required:true, message:"请输入host！",trigger:"blur"}
                ]
            },
        }
    },
    components: {},
    mounted(){
        this.$http.getHostList().then(res => {
            const host_list = res.data
            this.host_list = host_list
        })
    },
    methods: {
        initHostForm(){
            this.hostForm = {
                name: "",
                host: "",
                description:""
            }
        },

        onAddProjectButtonClick(){
            this.initHostForm()
            this.dialogType = "add"
            this.hostDialogVisible = true
        },

        onEditProject(host){
            this.initHostForm()
            this.hostForm = {
                id: host.id,
                name: host.name,
                host: host.host,
                description: host.description
            }
            this.hostDialogVisible = true
            this.dialogType = "edit"
        },
        //添加host功能
        onSubmitAddHost(){
            this.$refs['hostForm'].validate(valid =>{
                if(!valid){
                    return;
                }
                const params = JSON.parse(JSON.stringify(this.hostForm))
                params.project_id = this.project.id
                if(this.dialogType == "add"){
                    this.$http.addHost(params).then(res =>{
                        const host = res.data
                        // console.log(host);
                        this.hostDialogVisible = false
                        this.initHostForm()
                        this.project.host_list.push(host)
                        this.$message.success('恭喜！地址创建成功！')
                    })
                }else{
                    this.$http.editHost(this.hostForm.id,params).then(res =>{
                        if(res){
                            this.hostDialogVisible = false
                            this.initHostForm()
                            this.$message.success('修改成功')
                            const host = res.data
                            // console.log(host.id)
                            let index = 0
                            for(let loop_host of this.project.host_list){
                                // console.log(params);
                                // console.log('xxxxx');
                                if(loop_host.id == host.id){
                                    Vue.set(this.project.host_list,index,host)
                                    break
                                }
                                index++
                            }         
                        }   
                    })
                }   
            })
        },


        //删除功能
        deleteEditProject(id,index){
            // console.log(index)
            this.$confirm('此操作将永久删除该项目, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
                // var id = this.host_list[index].id
                // console.log(id)
                this.$http.delHost(id).then(res => {
                this.$loading.show()
                if(res && res.status == 204){
                    
                        // const project = res.data
                        // this.projects.splice(project,1)
                        this.project.host_list.splice(index,1)
                        // this.initProjectForm()
                        this.$loading.hide()
                        this.$message.success('恭喜！项目删除成功！')
                        }
                    });
            }).catch(() => {
                this.$message.info('已取消删除');          
            });
        },
    }
}
</script>

<style lang='scss' scoped>
.top-group{
    padding:10px 0;
}
</style>