<template>
<div class="">


    <div class="top-group">
        <!-- <el-form-item label="项目名称">
            <el-input placeholder="项目名称" ></el-input>
        </el-form-item> -->
        <el-button type="primary" icon="el-icon-search">搜索</el-button>
        <el-button type="primary" icon="el-ico-plus" @click="onAddProjectButtonClick">新增项目</el-button>
    </div>


    <el-table :data="projects.slice((currentPage-1)*pagesize,currentPage*pagesize)" style="width:100%">
        <el-table-column prop="name" label="项目名称" width="180" >
            <template slot-scope="scope">
                <!-- 新增鼠标移动到名称位置显示全部 -->
                <el-popover
                    placement="top-start"
                    width="200"
                    trigger="hover">
                    <slot>{{scope.row.name}}</slot>
                    <el-button slot="reference" type="text">{{scope.row.name|ellipsis}}</el-button>
                </el-popover>          
            </template>
        </el-table-column>
        <el-table-column prop="type" label="类型" width="100"></el-table-column>
        <el-table-column prop="description" label="描述" ></el-table-column>
        <el-table-column label="最后修改时间" width="200">
            <template slot-scope="scope">
                {{scope.row.last_update_time|dateFormat}}
            </template>
        </el-table-column>
        <el-table-column label="状态" width="100">
            <template>
                <!-- <el-tag type="success" v-if="scope.row.status==true">可用</el-tag>
                <el-tag type="danger" v-if="scope.row.status==false">禁用</el-tag> -->
                <el-tag type="success">可用</el-tag>
            </template>
        </el-table-column>
            <template>
                <el-tag type="success">可用</el-tag>
            </template>
        <el-table-column label="操作" width="300">
            <template slot-scope="scope">
                
                    <router-link :to="'project/'+scope.row.id" >
                        <el-button type="success" size="mini" style="margin-left:10px;margin-right:10px">进入项目</el-button>
                    </router-link>
                <el-button @click="onEditProject(scope.row)" size="mini">编辑</el-button>
                <el-button @click="deleteEditProject(scope.$index)" type="danger" size="mini">删除</el-button>
            </template>
        </el-table-column>
    </el-table>  
    <!-- 新建项目 -->
    <el-dialog title="新建项目" :visible.sync="addDialogVisiable" >
        <el-form ref="projectForm" :model="projectForm" label-width="80px" :rules="projectRules">
            <el-form-item label="名称" prop="name">
                <el-input v-model="projectForm.name" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="类型" prop="type">
                <el-select v-model="projectForm.type" placeholder="请选择类型">
                    <el-option label="Web" value="web"></el-option>
                    <el-option label="App" value="app"></el-option>
            </el-select>
            </el-form-item>
            <el-form-item label="描述" prop="description">
                <el-input type="textarea" v-model="projectForm.description" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisiable=false">取 消</el-button>
            <el-button type="primary" @click="onSubmitAddProject" :loading="addProjectButtonLoading">确 定</el-button>
        </div>
    </el-dialog>
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20, 40]" 
            :page-size="pagesize"         
            layout="total, sizes, prev, pager, next, jumper"
            :total="projects.length">    
    </el-pagination>
</div>
</template>

<script type="text/ecmascript-6">
import Vue from "vue";
export default {
    name: "ProjectList",

    //超过8个字符隐藏后面的
    filters: {
        ellipsis (value) {
        if (!value) return ''
        if (value.length > 8) {
            return value.slice(0,8) + '...'
        }
        return value
        }
    },
    data() {
        return {
            currentPage:1, //初始页
            pagesize:10,    //    每页的数据

            projects:[],
            addDialogVisiable: false,
            addProjectButtonLoading:false,
            delEditProject: false,
            dialogType: "add",
            projectForm:{
                name:"",
                type:"",
                description:""
            },
            projectRules:{
                name: [
                    {required:true, message:"请输入项目名称！",trigger:"blur"}
                ],
                type: [
                    {required:true, message:"请选择项目类型！",trigger:"blur"}
                ]
            }
        }
    },
    components: {},
    // mounted(){
    //     this.$http.getProjectList().then(res => {
    //         const projects = res.data
    //         this.projects = projects
    //     })
    // },
    created() {
        this.handleUserList()
    },
    methods:{
        // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
                this.pagesize = size;
                console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                console.log(this.currentPage)  //点击第几页
        },
         handleUserList() {
            this.$http.getProjectList().then(res => {  //这是从本地请求的数据接口，
                const projects = res.data
                this.projects = projects
            })
        },

        //初始化项目表单

        initProjectForm(){
            this.projectForm = {
                name:"",
                type:"",
                description:""
            }
        },
        onAddProjectButtonClick(){
            this.initProjectForm()
            this.dialogType = "add"
            this.addDialogVisiable = true
            // console.log(this.projectForm)
        },
        // 编辑，获取元素
        onEditProject(project){
            this.initProjectForm()
            this.projectForm = {
                name: project.name,
                type: project.type,
                description: project.description,
                id: project.id
            }
            this.addDialogVisiable = true
            this.dialogType = "edit"
        },

        //取消清空表单   @close('ref')
        close(formName){
            this.$refs[formName].resetFields();
        },
        //删除功能
        deleteEditProject(index){
            // console.log(index)
            this.$confirm('此操作将永久删除该项目, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
                var id = this.projects[index].id
                // console.log(id)
                this.$http.delProject(id).then(res => {
                if(res && res.status == 204){
                        // const project = res.data
                        // this.projects.splice(project,1)
                        this.projects.splice(index,1)
                        // this.initProjectForm()
                        this.$message.success('恭喜！项目删除成功！')
                        }
                    });
            }).catch(() => {
                this.$message.info('已取消删除');          
            });
        },
         
        //添加，编辑功能
        onSubmitAddProject(){
            this.$refs['projectForm'].validate(valid => {
                if (!valid) {
                    return
                }
                this.addProjectButtonLoading = true  
                if(this.dialogType == "add"){
                    this.$http.addProject(this.projectForm).then(res => {
                        this.addProjectButtonLoading =false
                        if(res && res.status == 201){
                            console.log(res)
                            const project = res.data
                            console.log(project)
                            this.projects.push(project)
                            this.addDialogVisiable=false
                            this.initProjectForm()
                            this.$message.success('恭喜！项目创建成功！')
                        }
                    })
                }else{
                    this.$http.editProject(this.projectForm.id,this.projectForm).then(res =>{
                        this.addProjectButtonLoading =false
                        if(res){
                            this.addDialogVisiable = false
                            this.initProjectForm()
                            this.$message.success('修改成功')
                            const project = res.data
                            let index = 0
                            for(let loop_project of this.projects){
                                // console.log(loop_project);
                                // console.log('xxxxx');
                                if(loop_project.id == project.id){
                                    Vue.set(this.projects,index,project)
                                    break
                                }
                                index++
                            }
                            
                        }
                        
                    })
                }
                    
            })
        }
    }

}
</script>

<style lang='scss' scoped>
.top-group{
    padding:10px 0;
    text-align: right;
}
</style>