import axios from "axios"
import auth from "./auth"


// const BASE_URL = "http://127.0.0.1:8000"
const BASE_URL = "http://182.92.84.4:8000"


class Http {
    constructor(){
        this.http = axios.create({
            baseURL: BASE_URL
        })

        //设置拦截器，用来添加 JWT Token的
        //从auth中获取token
        this.http.interceptors.request.use(config => {
            const token = auth.token;
            if (token && token != 'undefined') {
                // console.log("JWT " + token )
                config.headers.common.Authorization = "JWT " + token             
            }
            return config
        })

        //响应拦截器，如果返回403，跳转到登录页面
        //403没有权限访问这个接口
        this.http.interceptors.response.use(null, err => {
            if (err && err.response && err.response.status == 403){
                auth.clearUserToken()
            }
            return Promise.reject(err);
        }) 
    }
    login(params){
        const url = "/auth/login"
        return this.http.post(url,params)
    }

    //添加传入内容
    addProject(params){
        const url = "/project"
        return this.http.post(url,params)
    }

    //获取项目列表
    getProjectList(){
        const url = "/project"
        return this.http.get(url)
    }

    //删除指定ID项目
    delProject(project_id){
        const url = "/project/" + project_id
        return this.http.delete(url)
      }


    getHostList(){
        const url = "/host"
        return this.http.get(url)
    }


    //删除指定IDhost
    delHost(host_id){
        const url = "/host/" + host_id
        return this.http.delete(url)
      }

    //获取编辑项目ID
    editProject(project_id,params){
        const url = "/project/" + project_id
        return this.http.put(url,params)
      }

    getProjectDetail(project_id){
        const url = "/project/" + project_id
        return this.http.get(url)
      }
    
    //添加项目地址
    addHost(params){
        const url = "/host"
        return this.http.post(url,params)
    }
    //编辑项目地址
    editHost(host_id,params){
        const url = "/host/" + host_id
        return this.http.put(url,params)
    }

    addApi(params){
        const url = "/api"
        return this.http.post(url,params)
    }

    //删除API
    deleteApi(api_id){
        const url = "/api/" + api_id
        return this.http.delete(url)
    }

    editApi(api_id,params){
        const url = "/api/" + api_id
        return this.http.put(url,params)
    }

    runApi(api_id){
        const url = "/run/api/" + api_id
        return this.http.post(url)
    }

    //添加用例
    addCase(params){
        const url = "/case"
        return this.http.post(url,params)
    }
    // 编辑用例
    editCase(case_id,params){
        const url = "/case/" + case_id
        return this.http.put(url,params)
    }
    //运行用例
    runCase(case_id){
        const url = "/run/case/" + case_id
        return this.http.post(url)
    }

    //删除用例
    deleteCase(case_id){
        const url = "/case/" + case_id
        return this.http.delete(url)
    }

    getRunRecords(project_id,type){
        const url = "/record?project=" + project_id + "&type=" + type
        return this.http.get(url)
    }

    editUser(params){
        const url = "/auth/user"
        return this.http.put(url,params)
    }

    getUserList(){
        const url = "/auth/user"
        return this.http.get(url)
    }

}

export default new Http()